    <!-- Modal -->
<div class="modal fade mymodal" id="view-leaves" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" data-bs-backdrop="false" >
    <div class="modal-dialog modal-xl" role="document">
        <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">{{selected_employee?.name?.toUpperCase()}} LEAVE REQUEST HISTORY</h5>
                <button type="button" data-dismiss="modal" data-bs-dismiss="modal" aria-label="Close" class="btn-close" id="close-modal"></button>
            </div>
            <div class="modal-body">

        <div class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">


        <table class="table table-bordered table-striped">
  <thead>
    <tr class="text-dark">
      <th >Total Requests</th>
      <th >Approved Leaves</th>
      <th >Leave Balance</th>
      <th >Estimated Encashment</th>
      <th>Actions</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>{{ selected_employee.requests }}</td>
      <td>{{ selected_employee.approved_leaves }}</td>
      <td>{{ selected_employee.leave_balance }}</td>
      <td>{{ selected_employee.estimate_encashment}} </td>
      <td><button class="btn btn-primary" @click="requestEncashment()" v-if="selected_employee.encashment_count == 0"> Request Encashment</button> </td>
    </tr>
    <tr>
        <td colspan="5" v-if="selected_employee?.remark">Remark : {{ selected_employee?.remark }}</td>
    </tr>

  </tbody>
</table>



                        <v-client-table :data="leaves" :columns="columns2" :options="table_option">
<template #status="props">
  <span
    v-if="props.row.status"
    class="badge inv-status"
    :class="'badge-' + props.row.status.color"
  >
    {{ props.row.status.name }}
  </span>
</template>

                            <template #actions="props">

  <svg
  v-if="props.row.attachment"
  xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-eye text-primary" viewBox="0 0 16 16"
   data-bs-toggle="modal" data-bs-target="#view-leaves"
   @click="selected_employee = props.row"
  >
    <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z"/>
    <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0"/>
  </svg>
                            
 
                                
                            </template>
                           
