    </div>
</div>
 <!-- modal ends -->

</template>
<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>
<script setup>
    import { onMounted, ref ,inject } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    useMeta({ title: 'Custom Table' });
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    const axiosInstance = axios.create({
       baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const printEmployees = inject('printEmployees')
    const showMessage = inject('showMessage')
    const showError = inject('showError')
    const formatNumber = inject('formatNumber')
    const showConfirmation = inject('showConfirmation')
    const name = ref('');
    const columns = ref(['Sno', 'name','requests','approved_leaves','leave_balance','estimate_encashment','actions']);
    const columns2 = ref(['Sno', 'start_date','end_date','leave_days','status','remark','actions']);

    const items = ref([]);
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 , 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['id', 'service_name'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });


       const addLeave = ref(false)

const selected_employee = ref('')
const leaves = ref([])
const fillLeaves=(employee)=>{
  selected_employee.value = employee

  leaves.value = employee.leaves.map((item, index) => {
         const start = new Date(item.start_date)
         const end = new Date(item.end_date)
         const leave_days = Math.ceil((end - start) / (1000 * 60 * 60 * 24)) + 1
         return {
            ...item,
            Sno: index + 1,
            leave_days
         }
      })
}
    const employeesList = ref([])

const getEmployees = async () => {
  const response = await axiosInstance.get('employees')

  employeesList.value = response.data.map((item, index) => {
    const approved_leaves = (item.leaves ?? [])
    .filter(leave => leave.status_id === 3)
    .reduce((total, leave) => {
      if (!leave.start_date || !leave.end_date) return total

      const start = new Date(leave.start_date)
      const end = new Date(leave.end_date)
      const days = Math.ceil((end - start) / (1000 * 60 * 60 * 24)) + 1

      return total + days
    }, 0)

    return {
      ...item,
      Sno: index + 1,
      requests: item.leaves?.length ?? 0,
      total_leaves: 0,
      approved_leaves : approved_leaves + ' days',
      leave_balance : 28 - approved_leaves,
      encashment : (item.salary * (28 - approved_leaves)/28)  ,
      estimate_encashment : formatNumber(item.salary * (28 - approved_leaves)/28)  
    }
  })
}

const requestEncashment = async () => {
    const payload = {
        employee_id: selected_employee.value.id,
        issuer_id: JSON.parse(localStorage.getItem("user")).id,
        amount : selected_employee.value.encashment
    }

  
    try{
    await axiosInstance.post('leave-encashments',payload);
    document.getElementById('close-modal').click()
    showMessage('Encashment Requested')
    getEmployees()
    }catch(error){
        showError(error.message)
        console.log(error)
    }finally{

    }

}





    onMounted(() => {
        getEmployees()
    });
</script>
